@import url(https://fonts.googleapis.com/css2?family=Raleway&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/*Global variable*/
:root{
    --main-red:rgb(211,9,19);
    --main-dark:rgb(20,20,20);
    --main-deep-dark:rgb( 0,0,0);
    --main-deep-red:rgb(255,0,0);
    --main-gray:rgb(153,153,153);
    
}
*{
 margin:0;
 box-sizing: border-box;
}
body{
    font-family: 'Helvetica';
    -webkit-font-smoothing:antialiased;
    background: rgb( 0,0,0);
    background: var(--main-deep-dark);
    color: #fff;
}
a{
    text-decoration: none;
}
.header-container{
    width: 100vw;
    height:100%;
    position: relative;
    background: radial-gradient(rgba(0,0,0,0),rgba(0,0,0,0.7)),
    url(/netflixclone/static/media/netflix-background.33770dcd.jpg)no-repeat center center/cover;
}
.header-container::after{
content: '';
top:0;
left:0;
position: absolute;
width: 100%;
height: 100%;
background: rgba(0,0,0,0.5);
box-shadow: inset 4.375rem 3.125rem 11.875rem #000,inset -4.375rem -3.125rem -11.875rem;
}
/* -------------------main-login------------*/
.main-container
{
 width: 100%;
height: 110%;
position: relative;
background: radial-gradient(rgba(0,0,0,0),rgba(0,0,0,0.7)),
url(/netflixclone/static/media/netflix-background.33770dcd.jpg)no-repeat center center/cover;
}
.main-container::after{
    content: '';
    top:0;
    left:0;
    position: absolute;
    width: 100%;
    height: 100%;
    background: rgba(0,0,0,0.5);
    box-shadow: inset 4.375rem 3.125rem 11.875rem #000,inset -4.375rem -3.125rem -11.875rem;
    }
.tab-data{
    position: relative;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
}
.tab-row{
    padding: 0 0 0 -20px;
    background-color: #170f0f;
    margin: 0 0  0 0.1rem;
    width: 100vw;
    display: grid;
    align-items: center;
    text-align: center;
    justify-content: center;
    grid-template-columns: repeat(3 ,1fr);
    grid-gap:0.1rem;
    color: var(--main-gray);
    list-style: none;
}
@media(max-width:600px)
{
    .tab-row{
        width:100vh
    }
}
/* tab hover*/
ul li:hover{
    cursor: pointer;
    fill: #fff;
}
ul li:hover svg path{
    fill: #fff;
    color: #170f0f;
}
.active{
    border-bottom:4px solid var(--main-red);
    background: none;
    text-decoration: none;
    list-style:noneS;
    color: #fff;
    
}
.tab1{
    width:300px;
    height:4.6875rem;
    margin-top:15px;
    fill:var(--main-gray);
}   
